*{padding:0;margin:0}::-moz-selection{background:var(--primary-color);color:#fff}::selection{background:var(--primary-color);color:#fff}@page{margin:0}@font-face{font-family:Inter UI;font-style:normal;font-weight:400;src:url(/static/Inter-UI.var.woff2) format("woff2-variations"),url(/static/Inter-UI-Regular.woff2) format("woff2"),url(/static/Inter-UI-Regular.woff) format("woff")}body,html{font-size:16px}@media print{body,html{font-size:10px;-webkit-print-color-adjust:exact}}body{font-family:InterUI,BlinkMacSystemFont,Helvetica Neue,Segoe UI,Oxygen,Ubuntu,Cantarell,Open Sans,sans-serif;font-weight:400;line-height:1.5;color:#24292e}small{font-size:1rem;color:#6f7780}h1,h2{font-weight:400}h2{line-height:1.25}p{margin-top:1rem}ul{list-style:none}li{padding:3px 0}a{color:var(--primary-color);text-decoration:none;border-bottom:2px solid transparent;transition:all .25s ease;max-width:85%;overflow:hidden;text-overflow:ellipsis;display:inline-block;vertical-align:middle}a:hover{color:var(--primary-hover-color);border-color:var(--primary-hover-color)}:global(.container){margin:0 auto;padding-right:24px;padding-left:24px;max-width:1200px}@media (min-width:940px),print{:global(.container){padding-right:48px;padding-left:48px}}.layout{background:#f6f7f8}